"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Loader2, UserPlus, Edit, Trash2, Shield } from "lucide-react"
import { useToast } from "@/components/ui/use-toast"
import { useAuth } from "@/contexts/AuthContext"
import apiClient from "@/services/apiClient"

interface Usuario {
  id: number
  nome: string
  username: string
  email: string
  cargo: string
  perfil: string
}

export default function ListaUsuarios() {
  const router = useRouter()
  const { toast } = useToast()
  const { refreshAuth } = useAuth()

  const [usuarios, setUsuarios] = useState<Usuario[]>([])
  const [carregando, setCarregando] = useState(true)
  const [erro, setErro] = useState<string | null>(null)

  useEffect(() => {
    const carregarUsuarios = async () => {
      try {
        setCarregando(true)
        setErro(null)

        // Tentar renovar o token antes de fazer a requisição
        try {
          await refreshAuth()
        } catch (error) {
          console.error("Erro ao renovar token:", error)
        }

        const response = await apiClient.get("/usuarios")
        setUsuarios(response.data)
      } catch (error: any) {
        console.error("Erro ao carregar usuários:", error)
        setErro(`Não foi possível carregar a lista de usuários. ${error.message || ""}`)

        // Verificar se é um erro de autenticação
        if (error.response && error.response.status === 401) {
          toast({
            title: "Sessão expirada",
            description: "Sua sessão expirou. Por favor, faça login novamente.",
            variant: "destructive",
          })
          setTimeout(() => router.push("/login"), 2000)
        }
      } finally {
        setCarregando(false)
      }
    }

    carregarUsuarios()
  }, [router, toast, refreshAuth])

  const handleExcluirUsuario = async (id: number) => {
    if (!confirm("Tem certeza que deseja excluir este usuário?")) {
      return
    }

    try {
      await apiClient.delete(`/usuarios/${id}`)

      toast({
        title: "Sucesso",
        description: "Usuário excluído com sucesso.",
      })

      // Atualizar a lista de usuários
      setUsuarios(usuarios.filter((usuario) => usuario.id !== id))
    } catch (error: any) {
      console.error("Erro ao excluir usuário:", error)

      toast({
        title: "Erro",
        description: `Não foi possível excluir o usuário. ${error.message || ""}`,
        variant: "destructive",
      })
    }
  }

  if (carregando) {
    return (
      <div className="flex justify-center items-center h-screen">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
        <p className="ml-2">Carregando usuários...</p>
      </div>
    )
  }

  if (erro) {
    return (
      <div className="container mx-auto py-6">
        <h1 className="text-2xl font-bold mb-6">Usuários</h1>
        <div className="bg-red-50 border border-red-200 text-red-700 p-4 rounded-md">
          <p>{erro}</p>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto py-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Usuários</h1>
        <Button onClick={() => router.push("/usuarios/novo")}>
          <UserPlus className="mr-2 h-4 w-4" />
          Novo Usuário
        </Button>
      </div>

      {usuarios.length === 0 ? (
        <p className="text-center py-8 text-gray-500">Nenhum usuário encontrado.</p>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {usuarios.map((usuario) => (
            <Card key={usuario.id} className="overflow-hidden">
              <CardContent className="p-4">
                <div className="flex flex-col h-full">
                  <div className="mb-4">
                    <h2 className="text-xl font-semibold">{usuario.nome}</h2>
                    <p className="text-sm text-gray-500">{usuario.username}</p>
                    <p className="text-sm text-gray-500">{usuario.email}</p>
                    <div className="flex items-center mt-2">
                      <span className="text-xs bg-gray-100 text-gray-800 px-2 py-1 rounded-full">{usuario.cargo}</span>
                      {usuario.perfil && (
                        <span className="text-xs bg-blue-100 text-blue-800 px-2 py-1 rounded-full ml-2">
                          {usuario.perfil}
                        </span>
                      )}
                    </div>
                  </div>

                  <div className="flex gap-2 mt-auto">
                    <Button variant="outline" size="sm" onClick={() => router.push(`/usuarios/editar/${usuario.id}`)}>
                      <Edit className="h-4 w-4 mr-1" />
                      Editar
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => router.push(`/usuarios/perfis/manual?id=${usuario.id}`)}
                    >
                      <Shield className="h-4 w-4 mr-1" />
                      Perfil
                    </Button>
                    <Button variant="destructive" size="sm" onClick={() => handleExcluirUsuario(usuario.id)}>
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
